.section {
  width: 100%;
  max-width: 1400px;
  margin: auto;
  padding: 80px 40px;
}

/* TOP ROW */
.topRow {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.left {
  flex: 1;
}

.tag {
  background: #f2f4ff;
  padding: 8px 15px;
  font-size: 14px;
  color: #1b3a90;
  border-radius: 20px;
  font-weight: 600;
}

.heading {
  font-size: 48px;
  color: #0d1440;
  font-weight: 700;
  margin-top: 20px;
  line-height: 1.2;
}

/* RIGHT SIDE BUTTON */
.btn {
  background: #0d1440;
  color: white;
  padding: 16px 30px;
  border: none;
  border-radius: 40px;
  font-size: 18px;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
}

.btn svg {
  color: #0d1440;
  background: white;
  border-radius: 50%;
  padding: 5px;
}

/* BLOG CARDS */
.cardsWrapper {
  margin-top: 60px;
  display: flex;
  gap: 60px;
}

.card {
  flex: 1;
}

.cardImage {
  width: 100%;
  height: auto;
  border-radius: 18px;
  object-fit: cover;
}

.content {
  margin-top: 20px;
}

.date {
  font-size: 16px;
  color: #555;
}

.title {
  font-size: 28px;
  font-weight: 700;
  color: #0d1440;
  margin: 8px 0;
}

.desc {
  font-size: 17px;
  color: #444;
  line-height: 1.5;
}

/* ROUND BUTTON */
.roundBtn {
  margin-top: 18px;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  border: 1px solid #e0e0e0;
  background: white;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.roundBtn:hover {
  background: #f5f5f5;
}
