.section {
  width: 100%;
  background: linear-gradient(135deg, #ffffff 0%, #b6baf9 100%);
  padding: 80px 40px;
  border-radius: 18px;
}

.container {
  max-width: 1400px;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 60px;
}

.left {
  flex: 1;
}

.right {
  flex: 1;
  display: flex;
  justify-content: center;
}

.tag {
  background: #e9edfb;
  padding: 8px 16px;
  border-radius: 12px;
  font-size: 14px;
  color: #1b3a90;
  font-weight: 600;
}

.heading {
  font-size: 48px;
  font-weight: 700;
  color: #0d1440;
  margin: 20px 0 40px;
}

.form {
  width: 100%;
}

.row {
  display: flex;
  gap: 25px;
  margin-bottom: 25px;
}

