.field,
.fieldFull {
  display: flex;
  flex-direction: column;
}

.field input,
.fieldFull input {
  padding: 14px 14px;
  border: none;
  border-bottom: 1px solid #d7d7d7;
  outline: none;
  font-size: 16px;
  background: transparent;
}

.fieldFull textarea {
  padding: 14px 14px;
  border: none;
  border-bottom: 1px solid #d7d7d7;
  outline: none;
  height: 80px;
  resize: none;
  background: transparent;
}

.field label,
.fieldFull label {
  font-size: 15px;
  font-weight: 500;
  margin-bottom: 6px;
  color: #324268;
}

.btn {
  margin-top: 25px;
  padding: 15px 35px;
  background: #0d1440;
  color: #fff;
  border-radius: 40px;
  border: none;
  cursor: pointer;
  font-size: 17px;
  font-weight: 600;
}

.imageBox {
  border-radius: 20px;
  overflow: hidden;
}

.heroImg {
  width: 100%;
  height: auto;
  border-radius: 20px;
  object-fit: cover;
}
